.avatar-32, .avatar-24 {
  display: block;
  overflow: hidden;
  background-image: url("./assets/images/img-user-default.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  border-radius: 50%;
}
.avatar-32 img, .avatar-24 img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-24 {
  width: 24px;
  height: 24px;
}

.avatar-32 {
  width: 32px;
  height: 32px;
}

.btn-ghost, .btn-outlined, .btn-secondary, .btn-primary {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 8px;
  font-weight: 700;
  border-radius: 4px;
}
.btn-ghost:disabled, .btn-outlined:disabled, .btn-secondary:disabled, .btn-primary:disabled {
  cursor: not-allowed;
  opacity: 0.4;
}

.btn-primary {
  color: #fff;
  background-color: #3da5f5;
  transition: background-color 200ms ease-in-out;
}
.btn-primary:not(:disabled):hover {
  background-color: #3186c4;
}

.btn-secondary {
  color: #3f4150;
  background-color: #e0e2e7;
  transition: background-color 200ms ease-in-out;
}
.btn-secondary:not(:disabled):hover {
  background-color: #b2b3b9;
}

.btn-outlined {
  color: #3da5f5;
  background-color: #fff;
  border: 1px solid #3da5f5;
  transition: background-color 200ms ease-in-out;
}
.btn-outlined:not(:disabled):hover {
  background-color: #ecf6fe;
}

.btn-ghost {
  color: #3f4150;
  background-color: transparent;
  transition: color 200ms ease-in-out;
}
.btn-ghost:not(:disabled):hover {
  color: #8c8d96;
}

.btn-32 {
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.01em;
  height: 32px;
}

.btn-40 {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  height: 40px;
}

.btn-48 {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  height: 48px;
}

.btn-55 {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: -0.02em;
  height: 55px;
}

.form-select,
.form-input {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: #3f4150;
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 16px;
  background-color: #fff;
  border: 1px solid #e0e2e7;
  border-radius: 4px;
  transition: background-color 200ms ease-in-out;
  appearance: none;
}
.form-select::placeholder,
.form-input::placeholder {
  color: #b2b3b9;
}
.form-select:not(:disabled):hover,
.form-input:not(:disabled):hover {
  background-color: #f7f8fa;
}
.form-select:disabled,
.form-input:disabled {
  cursor: not-allowed;
  opacity: 0.4;
}

/* NOTE: Select */
.select-group {
  position: relative;
}
.select-group.is-active .form-select {
  border-color: #3da5f5;
}
.select-group.is-active .ic-caret {
  color: #3da5f5;
}
.select-group .form-select {
  padding-right: 40px;
  text-overflow: ellipsis;
}
.select-group .ic-caret {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  font-size: 24px;
  color: #b2b3b9;
  pointer-events: none;
}

/* NOTE: Select */
.input-group {
  position: relative;
}
.input-group .form-input {
  padding-left: 40px;
}
.input-group .ic-search {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  font-size: 20px;
  color: #8c8d96;
}

