/*
 * MoBILE (<768px) --------------------------------
 * fixture                             sm
 * # of columns                        4
 * gutter                              20px
 * margin                              5px
 * container-size                      100% - (5px * 2)
*
 * TABLET (≥768px) --------------------------------
 * fixture                             md
 * # of columns                        12
 * gutter                              20px
 * margin                              30px
 * container-size                      100% - (30px * 2)
 * max-width                           960px
*
 * DESKTOP (≥1200px) --------------------------------
 * fixture                             lg
 * # of columns                        12
 * gutter                              20px
